const API_URL = 'https://wed.thecoolpartsguy.com/spa/'

export async function get(resource, queryParams = {}, options = {}) {
  const query = queryParams && Object.keys(queryParams).length ? '?' + this.queryParams(queryParams) : '';
  return await raiseHTTPException(fetch, `${API_URL}${resource}${query}`, {
    method: 'GET',
    ...options,
  });
}

export async function post(resource, body = {}, options = {}) {
  return await raiseHTTPException(fetch, `${API_URL}${resource}`, {
    method: 'POST',
    body: JSON.stringify(body),
    ...options,
  });
}

