<script>
  import { onMount } from 'svelte';
  import { Router, Route } from 'svelte-routing';
  import { get } from '../services/fetch';

  // components for this layout
  import Header from '../components/Header.svelte';
  import SideBar from '../components/Sidebar/SideBar.svelte';

  // pages for this layout
  import DashBoard from '../views/DashBoard.svelte';
  import Users from '../views/Users.svelte';

  export let dashboard = '';
  let closedSidebar = false;
  let openMobileSidebar = false;
  let ready = false;
  let currentUser;
  let noOfUsers;

  onMount(async () => {
    currentUser = await get('user');
    noOfUsers = await get('users/count');
    ready = true;
  });
</script>

<div
  class="app-container app-theme-white body-tabs-shadow fixed-sidebar fixed-header"
  class:closed-sidebar={closedSidebar}
  class:sidebar-mobile-open={openMobileSidebar}
>
  {#if ready}
    <Router url="dashboard">
      <Header bind:closedSidebar bind:openMobileSidebar {currentUser} />
      <div class="app-main">
        <SideBar />
        <Route path="/" component={DashBoard} noOfUsers={noOfUsers.count} />
        <Route path="users" component={Users} />
      </div>
    </Router>
  {/if}
</div>
